<template>
  <div class="userMenus">
    <div class="container">
      <el-menu :default-active="menuId" class="el-menu-demo" mode="horizontal">
        <el-menu-item index="Main" @click="choiceView('Main')">
          网站首页
        </el-menu-item>
<!--        <el-menu-item index="Introduction"  @click="choiceView('Introduction')">-->
<!--          课程介绍-->
<!--        </el-menu-item>-->
        <el-menu-item index="Videos" @click="choiceView('Videos')" class="mobileShow">
          教学视频
        </el-menu-item>
        <el-menu-item index="Files" @click="choiceView('Files')" class="mobileShow">
          教学课件
        </el-menu-item>
        <el-submenu index="Resource" class="pcShow">
          <template slot="title">教学资源</template>
          <el-menu-item index="Videos" @click="choiceView('Videos')">教学视频</el-menu-item>
          <el-menu-item index="Files" @click="choiceView('Files')">教学课件</el-menu-item>
        </el-submenu>
      </el-menu>
    </div>
  </div>
</template>

<script>
  import {mapState} from 'vuex'
  export default {
    name: 'myUserMenus',
    computed:{
      ...mapState(['menuId'])
    },
    methods:{
      //组件跳转
      choiceView(name){
        this.$router.push({name})
      },
    }
  }
</script>

<style lang="less">
  .userMenus{
    border-bottom: 1px solid rgb(233,233,233);
    .el-menu{
      border-bottom: 0;
      li,.el-submenu__title{
        height: 49px;
        line-height: 49px;
        font-size: 15px;
        font-weight: bold;
        letter-spacing: 3px;
      }
      .is-active{
        color: #409EFF!important;
      }
    }
  }
  @media screen and(max-width: 499px){
    .userMenus{
      .el-menu{
        li{
          padding: 0 10px;
        }
      }
    }
  }
</style>



